import React, { useState } from 'react';
import { RiCameraLine } from 'react-icons/ri';

const ImageUploader = ({ img, isBack }) => {
    const [hovered, setHovered] = useState(false);

    return (
        <div className="flex">
            <label
                htmlFor="file-input"
                className={`relative cursor-pointer ${isBack ? "" : 'w-32'}`}
                onMouseEnter={() => setHovered(true)}
                onMouseLeave={() => setHovered(false)}
            >
                <input type="file" id="file-input" className="hidden" />
                {isBack ? <img
                    className={`h-[300px] w-auto object-cover transition-opacity duration-500 ${hovered ? 'opacity-50' : 'opacity-100'}`}
                    src={img}
                    alt="wait"
                /> :
                    <img
                        src={img}
                        alt="Upload"
                        className={`w-32 h-32 object-cover shadow-md shadow-gray-400 rounded-full transition-opacity duration-500 ${hovered ? 'opacity-50' : 'opacity-100'}`}
                    />}
                {hovered && (
                    <span className="absolute inset-0 flex items-center justify-center">
                        <div className={`bg-inherit rounded-full flex items-center justify-center  ${isBack ? 'w-40 h-40' : 'w-16 h-16'}`}>
                            <RiCameraLine className={`text-white ${isBack ? 'w-36 h-36' : 'w-12 h-12'}`} />
                        </div>
                    </span>
                )}
            </label>
        </div>
    );
}

export default ImageUploader;
